@import '../../styles/main.scss';



.container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  // gap: 14px;
  padding: 13px 16px;
  min-width: $mobile;
  margin: 0 auto;
  height: 60px;
  // outline: 1px solid black;

  @media screen and (min-width: $tablet) {
    padding: 13px 32px;
    width: $tablet;
  }

  @media screen and (min-width: $desktop) {
    width: $desktop;
    padding: 13px 60px;
  }
}

.logo {
  width: 104px;
  height: 26px;
}

.svg {
  width: 100%;
  height: 100%;
}


.nav {
  @extend %list;
  display: flex;
  gap: 10px;
}

