
@import '../../styles/main.scss';

.section {
  margin-top: 140px;
  text-align: center;
  margin-bottom: 140px;
}

.container {
  max-width: 328px;
  margin: 0 auto;

  @media screen and (min-width: $tablet) {
    max-width: 704px;
  }

  @media screen and (min-width: $desktop) {
    max-width: 904px;
  }
}

.title{
font-size: 40px;
font-weight: 400;
line-height: 40px;
text-align: center;
}

.usersList {
  @extend %list;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 20px;
    margin: 50px auto;
  
  @media screen and (min-width: $tablet) {
    flex-direction: row;
    flex-wrap: wrap;
    gap: 16px;
  }
}

.userCard{
 background-color: #fff;
 padding: 20px;
 border-radius: 10px;
 width: 328px;

 @media screen and (min-width: $tablet) {
  width: 344px;
 }

 @media screen and (min-width: $desktop) {
  width: 282px;
 }

}

.cardContainer {
  text-align: center;
  overflow: visible;
}

.avatar {
  width: 70px;
  height: 70px; 
  border-radius: 50%;
  margin: 0 auto;
}

.name {
  margin: 20px 0;
}

.textWrapper {
  position: relative;
  overflow: visible;
}
.position,
.email,
.phone,
.name {
  display: inline-flex;
  line-height: 26px;
  cursor: pointer;
}

.position:hover:after,
.email:hover:after,
.phone:hover:after,
.name:hover:after {
    content: attr(data-tooltip);
    position: absolute;
    padding: 3px 16px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    background: $text-main-color;
    color: $text-secondary-color;
    border-radius: 4px;
    white-space: nowrap;
    bottom: -200%;
    left: 50%;
    transform: translate(10px, -50%);
    z-index: 1000;
    opacity: 1;
    overflow: visible;
}

.name:hover:after {
    bottom: -50%;
}



.btnWrapper {
  display: inline-block;
}

.btn {
  margin: 0 auto;
}


.loader {
  display: block;
  margin: 0 auto;
  width: 50px;
  height: 50px;
  margin-bottom: 50px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}